.container {
  height: 340px;
  width: 180px;
  background: rgba(212, 234, 246, 0.23);
  padding: 10px;
}
.head {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 20px auto 10px;
  display: block;
}
.img {
  width: 18px;
  border-radius: 50%;
  margin-left: -6px;
  margin-right: 5px;
  margin-top: -5px;
}
.link {
  text-decoration: none !important;
}
.title {
  margin-top: 10px;
  font-size: 18px;
  color: #000000;
  display: inline;
  line-height: 22px;
}
.descCon {
  margin-top: 10px;
}
.descCon li {
  text-align: left;
  font-size: 12px;
  color: #4a4a4a;
  line-height: 16px;
}



