.container {
  position: relative;
  vertical-align: top;
  height: 390px;
  width: 190px;
  display: inline-block;
  text-align: left;
  padding: 20px;
  margin-left: 10px;
  -webkit-transition: background 0.3s;
  -o-transition: background 0.3s;
  transition: background 0.3s;
}
.container:hover {
  background: #ffffff;
  -webkit-box-shadow: 1px 5px 10px rgba(0, 0, 0, 0.1);
          box-shadow: 1px 5px 10px rgba(0, 0, 0, 0.1);
}
.divider {
  height: 30px;
}
.title {
  font-size: 18px;
  color: #4a4a4a;
  letter-spacing: 0;
  line-height: 22px;
  height: 55px;
}
.tip {
  font-size: 12px;
  color: #9b9b9b;
  letter-spacing: 0;
  line-height: 24px;
}
.link {
  border: 2px solid WHITE;
  padding: 2px;
  color: white;
  display: inline-block;
  margin-top: 100px;
}
.next {
  text-align: center;
  color: white;
  display: inline-block;
  margin-top: 100px;
}



