.container {
  background: #e8e9eb;
  border: 1px solid #f5f5f5;
  font-size: 14px;
  color: #323232;
  line-height: 26px;
  text-align: center;
  min-height: 150px;
}
.content {
  background: #e8e9eb;
  padding: 20px 10px;
  width: 1200px;
  height: 100%;
  margin: 0 auto;
}

.tab {
  text-align: left;
  width: 50%;
  display: inline-block;
  -moz-tab-size: 14px;
    -o-tab-size: 14px;
       tab-size: 14px;
  vertical-align: top;
}
.tip {
  margin-left: 16px;
}



